body {
  margin: 0px;
  padding: 0px;
  box-sizing: border-box;
}

.div_1 {
  width: 100%;
  height: auto;
  background-color: rgb(224, 224, 224);
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: row;
  flex-wrap: wrap;
}

.big_card {
  width: 320px;
  height: auto;
  border-radius: 17px;
  margin: 70px 40px;
  padding-bottom: 15px;
  box-shadow: 0 4px 5px 0 rgb(0 0 0 / 20%), 0 0 3px 0 rgb(0 0 0 / 10%);
  background-color: white;
}

.big_card:hover {
  transform: scale(1.01, 1.01);
}

.img {
  width: 100%;
  height: 250px;
  border-radius: 17px 17px 0px 0px;
  object-fit: cover;
}

.small_div {
  padding: 10px 20px;
}

.card_span {
  text-transform: capitalize;
  color: rgb(181 145 145);
  display: block;
  font-family: sans-serif;
  font-size: 17px;
}

.card_head {
  margin-top: 5px;
  text-transform: capitalize;
  font-family: verdana;
}

.card_btn {
  margin-top: 15px;
  width: 100%;
  height: 40px;
  border-radius: 4px;
  border: none;
  cursor: pointer;
  color: white;
  outline: none;
  background: linear-gradient(
      90deg,
      var(--c1, #9921e8),
      var(--c2, #5f72be) 51%,
      var(--c1, #5f72be)
    )
    var(--x, 0) / 200%;
  font-size: 17px;
  font-family: sans-serif;
  box-shadow: 1px 1px 10px white;
}

.card_btn:hover {
  background: linear-gradient(
      90deg,
      var(--c1, #fc5296),
      var(--c2, #f67062) 51%,
      var(--c1, #5f72be)
    )
    var(--x, 0) / 200%;
}

.card_btn:active {
  transform: scale(1.1, 1.1);
}

@media (max-width: 505px) {
  .main_head {
    font-size: 25px;
  }
}

@media (max-width: 390px) {
  .main_head {
    font-size: 22px;
  }
}
